export function number(num, fix) {
    if (!isNaN(num)) {
        if (num == 0) return num;
        if (num < 0.000001 && num > 0) return '< 0.000001';
        return new BigNumber(num).toFormat(fix || 6, 1);
    }
    throw 'parameter not is number';
}

export function getSendValue(value, decimal = 18) {
    if (value?._isBigNumber || value?._hex) {
        value = value.toString();
    }
    const v = new BigNumber(value);
    const dv = new BigNumber(10 ** decimal);
    return v.multipliedBy(dv);
}

export function getResultValue(value, decimal = 18) {
    if (value?._isBigNumber || value?._hex) {
        value = value.toString();
    }
    const v = new BigNumber(value);
    const dv = new BigNumber(10 ** decimal);
    return v.dividedBy(dv);
}

