export function formatSeconds(value) {

    let result = '';
    if (!value) {
        return '0';
    }
    let sencond = parseInt(value);
    if (sencond > 3600 * 24) {  //
        let day = sencond / (3600 * 24);
        sencond = sencond % (3600 * 24);
        let minute = sencond % 3600 / 60; //
        let hour = sencond / 3600;          //1==60==36003600= 
        if (parseInt(hour) > 0) {
            if (parseInt(minute) > 0) {
                result = parseInt(day) + '' + parseInt(hour) + '' + parseInt(minute) + '';
            } else {
                result = parseInt(day) + '' + parseInt(hour) + '';
            }
        } else {
            result = result = parseInt(day) + '';
        }
    } else if (sencond > 3600) {     //
        let hour = sencond / 3600;
        let minute = sencond % 3600 / 60;         // 60 = 
        if (parseInt(minute) > 0) {
            result = parseInt(hour) + '' + parseInt(minute) + '';
        } else {
            result = parseInt(hour) + '';
        }
    } else if (sencond > 60) {  //
        let minute = sencond / 60;
        let sec = sencond % 60;           //
        if (parseInt(sec) > 0) {
            result = parseInt(minute) + '' + parseInt(sec) + '';
        } else {
            result = parseInt(minute) + '';
        }
    } else {  //
        result = '' + parseInt(sencond) + '';
    }
    return result;
}

