export function getDelayDay(delayDay, ignoreDay = true) {
    let days;
    const delay = dayjs(parseInt(delayDay))
        .format('YYYY-MM-DD HH:mm:ss');
    const now = dayjs()
        .format('YYYY-MM-DD HH:mm:ss');
    days = dayjs(delay)
        .diff(dayjs(now), 'days');
    if (days === 0) {
        if (ignoreDay) {
            days = 1;
        } else {
            days = dayjs(delay)
                .diff(dayjs(now), 'seconds');
            console.log(days)
            const daySecond = 60 * 60 * 24;
            days = days / daySecond;

            days = JcToFixed(days);
        }
    }
    return days;
}

/**
 * 
 * @param num
 * @returns {string}
 */
export function addDou(num) {
    if (num == 0) {
        return '0';
    }
    if (!num) {
        return '';
    }
    if (typeof num === 'number') {
        num = num.toFixed(2);
    }
    if (typeof num === 'string') {
        num = parseFloat(num)
            .toFixed(2)
            .toString();
    }

    const numArr = num.split('.');
    num = numArr[0];
    let result = '';
    while (num.length > 3) {
        result = ',' + num.slice(-3) + result;
        num = num.slice(0, num.length - 3);
    }
    if (num) {
        result = num + result;
    }
    result = result + '.' + numArr[1];
    return result;
}


