export function copyCot(cot) {
    const pEle = document.createElement('p');
    pEle.innerHTML = cot || '';
    document.body.appendChild(pEle);
    const range = document.createRange(); // range
    window.getSelection().removeAllRanges(); //selection
    range.selectNode(pEle); // 
    window.getSelection().addRange(range); // 
    const copyStatus = document.execCommand("Copy"); // copy
    Toast('')
    document.body.removeChild(pEle);
    window.getSelection().removeAllRanges(); //selection
}


export function gettimeText(data) {
    if (data && data > 0) {
        const cutTime = data;
        const day = Math.floor(cutTime / (60 * 60 * 24));
        const hour = Math.floor((cutTime / (60 * 60)) % 24);
        const minute = Math.floor((cutTime / 60) % 60);
        const second = Math.round(cutTime % 60);
        return `${day ? `${day}` : ''}${hour ? `${hour}` : ''}${
            minute ? `${minute}` : ''
        }${second ? `${second}` : ''}`;
    }
};
