import React, { useEffect, useState } from "react";
import "./index.scss";
import { useTranslation } from "react-i18next";
import Block from "../../component/Block";
import info from "../info";
import {
  getResultValue,
  getSendValue,
  gettimeText,
  JcToFixed,
  number,
  useInterval,
} from "../../utils/tool";
import Tab from "../../component/Tabs";
import StructureFundDescribe from "../component/StructureFundDescribe";
import { Button, Input, Pagination, Popup, Selector, Toast } from "react-vant";
import { DefaultDecimals } from "../../protocol/constants";
import {
  getCanWithdrawBeforeSync,
  getCurrencyPriceSync,
  getDepositListSync,
  getDynamicFunSync,
  getUserInfoSync,
  getWithdrawSync,
} from "../coinSFApi";
import UserStoreHook from "../../hook/UserStoreHook";
import dayjs from "dayjs";
import BigNumber from "bignumber.js";

export default function CoinStructuredFundDetail() {
  const [detailList, setDetailList] = useState([]);

  // 
  const [totalNum, setTotalNum] = useState(0);
  const [pagevalue, setPagevalue] = useState(1);
  // 
  const [userinfo, setUserinfo] = useState({});

  const [currentPrice, setCurrentPrice] = useState("0"); //

  // 
  const [showCloseIcon, setShowCloseIcon] = useState(false);
  const [showData, setShowData] = useState({});
  const [cycleKey, setCycleKey] = useState(""); //
  const [quchuNum, setQuchuNum] = useState(0); //
  const [isMaxNum, setIsMaxNum] = useState(false); //

  const [isCanBefore, setIsCanBefore] = useState(false); //

  const { setAddress, user } = UserStoreHook();
  const { t } = useTranslation();

  const getCoinPrice = async () => {
    // dev
    const res = await getCurrencyPriceSync();
    console.log(JcToFixed(getResultValue(res, DefaultDecimals.TRX)));
    setCurrentPrice(JcToFixed(getResultValue(res, DefaultDecimals.TRX)));
  };
  //time
  //amount
  //chanpinid
  //nonce
  //id

  const getDetailList = async () => {
    const res = await getDepositListSync(user.address, pagevalue, 10);
    console.log(res, "res");
    const userList = res.arrlist.map((item, index) => {
      const time =
        item[0] - dayjs().valueOf() / 1000 + info.options[item[2]].waitTime; // 
      const CoinNum = getResultValue(item[1], DefaultDecimals[info.symbol]);
      const profit = info.options[item[2]].profit;
      return {
        time,
        cangetPrice: time < 0,
        // coin
        symbolCount: JcToFixed(CoinNum),
        // TRX
        fractionPrice: JcToFixed(CoinNum * currentPrice),
        // 
        fixedSymbol: JcToFixed(CoinNum * profit),
        // coin
        fixedSymbolPrice: JcToFixed(CoinNum * profit * currentPrice),
        allCanGetNum: JcToFixed(CoinNum * (1 + profit)),
        id: item[4].toString(),
        isWithdraw: item[1].toString() == 0,
      };
    });
    setTotalNum(res.count);
    setDetailList([...userList]);
  };

  const getdynamicCoin = async () => {
    const res = await getUserInfoSync(user.address);
    if (res) {
      setUserinfo({
        dynamicCoin: JcToFixed(
          getResultValue(res, DefaultDecimals[info.symbol])
        ),
      });
    }
  };
  // 
  const getCanWithdrawBefore = async () => {
    let res = await getCanWithdrawBeforeSync();
    setIsCanBefore(res);
  };
  // 
  const getUerIfon = async () => {
    getdynamicCoin();
    getCoinPrice();
    getCanWithdrawBefore();
  };
  // 
  const getDynamicFun = async () => {
    // dev
    let res = await getDynamicFunSync();
    if (res) {
      Toast("");
      getdynamicCoin();
    }
  };

  // 
  const getWithdraw = async (data) => {
    let enterValue = quchuNum;
    let entercycle = cycleKey;
    let max = data.allCanGetNum;
    if (!data.cangetPrice) {
      max = data.symbolCount;
    }
    if (!quchuNum) {
      enterValue = 0;
    }
    if (quchuNum > max) {
      return Toast("");
    }
    let sendValue = new BigNumber(max).minus(enterValue)
    if (new BigNumber(Math.abs(sendValue)).isLessThan(0.000001)) {
      sendValue = 0
    }

    if (enterValue==0) {
      sendValue = "9999999";
      entercycle='0'
    } else {
      if (!cycleKey) {
        return Toast("");
      }
    }
    // dev
    let res = await getWithdrawSync(
      data.id,
      getSendValue(sendValue, DefaultDecimals[info.symbol]).toFixed(0, 1),
      entercycle
    );
    if (res) {
      Toast("");
      getDetailList();
    }
  };

  useInterval(() => {
    detailList.forEach((i) => {
      if (i.time > 0) {
        i.time -= 1;
      } else {
        i.cangetPrice = true;
      }
    });
    setDetailList([...detailList]);
  }, 1000);
  useEffect(() => {
    if (!user.address) setAddress();
    if (user.address) {
      getUerIfon();
    }
  }, [user.address]);
  useEffect(() => {
    if (currentPrice > 0) {
      getDetailList();
    }
  }, [pagevalue, currentPrice]);

  return (
    <div className="coinStructuredFundDetail">
      <StructureFundDescribe title={t("")} />

      <div className="detailtitle mb-15">{t("common.my_position")}</div>
      {detailList.length ? (
        <>
          {detailList.map((item, index) => (
            <Block key={index}>
              <div className="flex-row font-size-12 fl-jc-between mb-10">
                <div className="font-color-describe">
                  {t("page.coin.bianhao")}
                </div>
                <div className="font-color-white">{item.id}</div>
              </div>
              {/*  */}
              <div className="flex-row font-size-12 fl-jc-between mb-10">
                <div className="font-color-describe">
                  {t("page.coin.principal")}
                </div>
                <div className="font-color-white">
                  {item.symbolCount} {info.symbol}  {item.fractionPrice}{" "}
                  {info.principalSymbol}
                </div>
              </div>
              {/*  */}
              <div className="flex-row font-size-12 fl-jc-between mb-10">
                <div className="font-color-describe">
                  {t("page.coin.staticInterest")}
                </div>
                <div className="font-color-white">
                  {item.fixedSymbol} {info.symbol}  {item.fixedSymbolPrice}{" "}
                  {info.principalSymbol}
                </div>
              </div>
              {/*  */}
              {item.cangetPrice && !item.isWithdraw ? (
                <div className="flex-row font-size-12 fl-jc-between mb-10">
                  <div className="font-color-describe">
                    {t("")}
                  </div>
                  <div className="font-color-white">
                    {JcToFixed(item.fixedSymbol * info.profitFee)} {info.symbol}{" "}
                     {JcToFixed(item.fixedSymbolPrice * info.profitFee)}{" "}
                    {info.principalSymbol}
                  </div>
                </div>
              ) : null}

              {item.isWithdraw ? (
                <div className="info-button"></div>
              ) : !item.cangetPrice ? (
                <div className="flex-row fl-jc-between">
                  <div
                    style={{ width: isCanBefore ? "65%" : "100%" }}
                    className="info-button"
                  >
                    {gettimeText(item.time)}
                  </div>
                  {isCanBefore ? (
                    <div
                      className=" end-button-2"
                      onClick={() => {
                        setShowCloseIcon(true);
                        setShowData(item);
                      }}
                    >
                      
                    </div>
                  ) : null}
                </div>
              ) : (
                <div
                  className="main-button"
                  onClick={() => {
                    setShowCloseIcon(true);
                    setShowData(item);
                  }}
                >
                  /
                </div>
              )}
            </Block>
          ))}
          <div className="mt-10 ml-15 mr-15">
            <Pagination
              forceEllipses
              value={pagevalue}
              onChange={setPagevalue}
              totalItems={totalNum}
              itemsPerPage={10}
              mode="simple"
            />
          </div>
        </>
      ) : (
        <Block>
          <div className="nodetail">{t("common.no_record")}</div>
        </Block>
      )}
      <Popup
        visible={showCloseIcon}
        style={{ width: "80%", background: "#f6f8fc", padding: "1rem" }}
        title="/"
        closeable
        onClose={() => {
          setShowCloseIcon(false);
          setShowData({});
          setCycleKey('')
          setQuchuNum(0)
        }}
      >
        <div className="mb-10 ">{showData.id}</div>
        <div className="mb-10">
          {info.symbol}
          {showData.cangetPrice ? showData.allCanGetNum : showData.symbolCount}
        </div>
        <div style={{ background: "#fff" }} className="mb-10">
          <Input
            onChange={(e) => {
              setQuchuNum(e);
              setIsMaxNum(false);
            }}
            value={quchuNum}
            suffix={
              <Button
                size="small"
                onClick={() => {
                  setIsMaxNum(true);
                  setQuchuNum(
                    showData.cangetPrice
                      ? showData.allCanGetNum
                      : showData.symbolCount || 0
                  );
                }}
              >
                
              </Button>
            }
            placeholder=""
          />
        </div>
        <div className="mb-10">
          {info.symbol}
          {JcToFixed(
            (showData.cangetPrice
              ? showData.allCanGetNum
              : showData.symbolCount) - quchuNum
          )}
        </div>
        <div className="mb-10">
          <div className="mb-10"></div>
          <Selector
            options={info.options}
            defaultValue={[]}
            value={[cycleKey]}
            onChange={(arr, extend) => setCycleKey(extend.items[0].value)}
          />
        </div>
        <div className="mb-10 font-color-red t-a-right"></div>
        <div
          className="main-button"
          onClick={() => {
            getWithdraw(showData);
          }}
        >
          
        </div>
      </Popup>
      <div className="footertext">
        <div className="flex-row fl-jc-between mb-10 footerdynamic">
          <div>{t("page.coin.dynamicIncome")}</div>
          <div className="footercolors">
            {`${number(userinfo.dynamicCoin || 0, 2)} ${info.symbol}  ${number(
              (userinfo.dynamicCoin || 0) * currentPrice,
              2
            )}${info.principalSymbol}`}
          </div>
        </div>
        <div
          className="main-button"
          onClick={() => {
            getDynamicFun();
          }}
        >
          
        </div>
      </div>
    </div>
  );
}
